import {
	TOKENNAME,
	HTTP_REQUEST_URL,
	HEADER
} from "../config/app.js"
import store from "../store"

export default {
	/**
	 * this.Tips("/pages/test/test") 
	 * this.Tips({title: ''}, '/pages/test/test') 
	 * this.Tips({title: ''}, {tab: 1, url: '/pages/test/test'}) table
	 * tab = 1 table
	 * tab = 2 table
	 * tab = 3 
	 * tab = 4 
	 * tab = 5 
	 */
	Tips: (opt, to_url) => {
		if (typeof opt == 'string') {
			to_url = opt;
			opt = {}
		}
		let title = opt.title || '',
			icon = opt.icon || 'none',
			endtime = opt.endTime || 2000,
			success = opt.success

		if (title) {
			uni.showToast({
				title: title,
				icon: icon,
				duration: endtime,
				success
			})
		}

		if (to_url != undefined) {
			if (typeof to_url == 'object') {
				let tab = to_url.tab || 1,
					url = to_url.url || ''
				switch (tab) {
					case 1:
						// table
						setTimeout(function() {
							uni.navigateTo({
								url: url
							})
						}, endtime);
						break;
					case 2:
						//table
						setTimeout(function() {
							uni.navigateTo({
								url: url
							})
						}, endtime);
						break;
					case 3:

						//
						setTimeout(function() {

							uni.navigateBack({
								delta: parseInt(url),
							})




						}, endtime);
						break;
					case 4:
						//
						setTimeout(function() {
							uni.reLaunch({
								url: url,
							})
						}, endtime);
						break;
					case 5:
						//
						setTimeout(function() {
							uni.redirectTo({
								url: url,
							})
						}, endtime);
						break;
				}
			} else if (typeof to_url == 'function') {
				setTimeout(function() {
					to_url && to_url();
				}, endtime);
			} else {
				//
				setTimeout(function() {
					uni.navigateTo({
						url: to_url,
					})
				}, title ? endtime : 0);
			}
		}

	},

	/*
	 * 
	 */
	SplitArray(list, sp) {
		if (typeof list != 'object') return [];
		if (sp === undefined) sp = [];
		for (var i = 0; i < list.length; i++) {
			sp.push(list[i]);
		}
		return sp;
	},

	// Uni.createSelectorQuery
	getSelectorQueryInfo(selector) {
		return new Promise((resolve, reject) => {
			uni.createSelectorQuery().select(selector).boundingClientRect(data => {
				resolve(data)
			}).exec()
		})
	},

	// &
	convertObjectToString(options) {
		return Object.keys(options).map(item => `${item}=${options[item]}`).join("&")
	},

	/**
	 * 
	 * @param {*} s
	 */
	isPhone(value) {
		let regPhone = /^([0-9]{3,4}-)?[0-9]{8,9}$/
		let regMobile = /^1[3456789]\d{9}$/
		if (!regPhone.test(value) && !regMobile.test(value)) {
			return false
		} else {
			return true
		}
	},

	showToast({
		title,
		icon,
		duration,
		success = () => {}
	}) {
		return uni.showToast({
			title: title,
			icon: icon || 'success',
			duration: duration || 2000,
			success: success
		})
	},

	// 
	/**
	 * 
	 * 
	 * 
	 */
	serviceOrderQueryString(status, pageNo, pageSize, content, userId) {
		let obj = {
			pageNo: pageNo,
			pageSize: pageSize
		}

		if (status == 0 || status == 1) {
			obj.status = status
		} else if (status == 2) {
			obj.createBy = userId
		}
		
		if (content) {
			obj.content = content
		}

		return obj
	},

	// null
	convertNullToEmpty(obj) {
		for (let item in obj) {
			if (obj[item] == "null") {
				obj[item] = ""
			}
		}
		return obj
	},

	trim(str) {
		return String.prototype.trim.call(str);
	},
	uploadFile(file) {
		debugger
		return new Promise((resolve, reject) => {
			let header = HEADER
			header[TOKENNAME] = store.state.app.token;
			uni.uploadFile({
				url: `${HTTP_REQUEST_URL}/files/upload?filename=${file.name}`,
				name: 'file',
				header: header,
				filePath: file.path,
				success: (res) => {
					console.log('uploadImage success, res is:', res)
					resolve(res)
				},
				fail: (err) => {
					reject(err)
				},
			});
		})
	}

}